import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FormGroupConfig, gettext } from '@c8y/ngx-components';
import { get } from 'lodash-es';
import {
  BulkOperationType,
  OperationDetails
} from '@c8y/ngx-components/operations/bulk-operations-service';

@Component({
  selector: 'c8y-operation-details',
  templateUrl: 'operation-details.component.html'
})
export class OperationDetailsComponent implements OnInit {
  @Input()
  set bulkOperationType(bulkOperationType: BulkOperationType) {
    this.titlePlaceholder = get(this.placeholders[bulkOperationType], 'title');
    this.descriptionPlaceholder = get(this.placeholders[bulkOperationType], 'description');
  }

  titlePlaceholder: string = gettext('e.g. Reset factory settings');
  descriptionPlaceholder: string = gettext(
    'e.g. Reset factory settings of all devices of model XYZ'
  );
  fgOperationDescription: FormGroup;

  private placeholders: { [key in BulkOperationType]: { title: string; description: string } } = {
    [BulkOperationType.SOFTWARE]: {
      title: gettext('e.g. Cloud connectivity software'),
      description: gettext(
        'e.g. Cloud connectivity software applied to devices with type c8y_Linux'
      )
    },
    [BulkOperationType.FIRMWARE]: {
      title: gettext('e.g. Firmware for hardware revision b'),
      description: gettext(
        'e.g. Firmware for hardware revision b applied to devices with type c8y_Linux'
      )
    },
    [BulkOperationType.CONFIGURATION]: {
      title: gettext('e.g. Host configuration c8y_Linux'),
      description: gettext('e.g. Host configuration applied to devices with type c8y_Linux')
    },
    [BulkOperationType.DEVICE_PROFILE]: {
      title: gettext('e.g. Device profile for c8y_Linux devices'),
      description: gettext('e.g. Device profile applied to devices with type c8y_Linux')
    }
  };

  constructor(private formBuilder: FormBuilder) {}

  ngOnInit(): void {
    const config: FormGroupConfig<OperationDetails> = {
      description: ['', Validators.required],
      note: [''],
      schedule: []
    };

    this.fgOperationDescription = this.formBuilder.group(config);
  }
}
